left",staticStyle:{"display":"contents"},attrs:{"data-aos":"fade-left"}}),_c('p',{staticClass:"text-regular"},[_vm._v("    La clasificación supervisada requiere de cierto conocimiento previo del terreno y de los tipos de coberturas, a través de una combinación de trabajo de campo, análisis de fotografías aéreas, mapas e informes técnicos y referencias profesionales y locales. Con base en este conocimiento se definen y se delimitan sobre la imagen las áreas de entrenamiento o pilotos. Las características espectrales de estas áreas son utilizadas para “entrenar” un algoritmo de clasificación, el cual calcula los parámetros estadísticos de cada banda para cada sitio piloto, para evaluar cada ND de la imagen, compararlo y asignarlo a una respectiva clase. La clasificación supervisada pretende definir las clases temáticas que no tengan claro significado espectral, considerado por esto como un método artificial.")])]),_c('div',{staticClass:"bloque-texto-d__autor"},[_c('div',{staticClass:"h5 mb-0",staticStyle:{"display":"contents"}},[_vm._v("(Posada, 2008).")]),_c('i',{staticClass:"fas fa-quote-right",attrs:{"data-aos":"fade-left"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"contCaja p-3"},[_c('div',{staticClass:"tabla-a color-acento-botones",staticStyle:{"overflow":"initial"}},[_c('table',[_c('thead',{staticStyle:{"border":"none"}},[_c('tr',[_c('th',{staticClass:"text-white p-3 text-center",staticStyle:{"background-color":"#FF5151","text-align":"left","border":"none","width":"30%"}},[_vm._v("Nombre etapa")]),_c('th',{staticClass:"text-white p-3 text-center",staticStyle:{"background-color":"#FF5151","text-align":"left","border":"none","width":"70%"}},[_vm._v("Descripción")])])]),_c('tbody',[_c('tr',[_c('td',{staticClass:"p-5",staticStyle:{"background-color":"#E5F3F5"}},[_c('strong',[_vm._v("Análisis estadístico y visual de las bandas")])]),_c('td',{staticStyle:{"background-color":"#F6F6F6"}},[_c('p',[